.settings {
  padding: 16px;
  color: #333; }
  .settings h1 {
    font-size: inherit;
    font-weight: bold;
    margin: 3px 0 21px;
    color: #919191; }
  .settings .warning {
    color: red;
    display: none; }

.tester .meme-container {
  background: #DADADA;
  opacity: 0;
  position: relative;
  overflow: hidden; }

.tester .meme-img-container {
  overflow: hidden;
  height: 100%;
  position: relative;
  z-index: 1;
  padding-top: 56.25%; }
  .tester .meme-img-container img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    background: #111; }

.tester .feedback-text {
  min-height: 30px;
  font-weight: bold;
  color: #0DA00D; }
  .tester .feedback-text.fail {
    color: #A00D0D; }

.tester .spinner {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-color: #bbb; }
  .tester .spinner .circle {
    border-width: 13px; }

.hidden-tester {
  display: none; }

.material-radio {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  margin: 0.6rem 0; }
  .material-radio input {
    display: none; }

.material-radio-btn {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(0, 0, 0, 0.54);
  border-radius: 10px;
  vertical-align: middle;
  margin-right: 0.7rem;
  transition: border-color 0.2s ease-out; }
  .material-radio-btn::before {
    content: '';
    display: block;
    width: 10px;
    height: 10px;
    background: #3F51B5;
    border-radius: 5px;
    margin: 3px;
    opacity: 0;
    transform: scale(0);
    transition: all 0.2s ease-out;
    transition-property: opacity, transform; }
  :checked + .material-radio-btn {
    border-color: #3F51B5; }
    :checked + .material-radio-btn::before {
      opacity: 1;
      transform: scale(1); }

.material-radio-text {
  line-height: 1; }

@keyframes container-rotate {
  to {
    transform: rotate(360deg); } }

@keyframes fill-unfill-rotate {
  12.5% {
    transform: rotate(135deg); }
  /* 0.5 * ARCSIZE */
  25% {
    transform: rotate(270deg); }
  /* 1   * ARCSIZE */
  37.5% {
    transform: rotate(405deg); }
  /* 1.5 * ARCSIZE */
  50% {
    transform: rotate(540deg); }
  /* 2   * ARCSIZE */
  62.5% {
    transform: rotate(675deg); }
  /* 2.5 * ARCSIZE */
  75% {
    transform: rotate(810deg); }
  /* 3   * ARCSIZE */
  87.5% {
    transform: rotate(945deg); }
  /* 3.5 * ARCSIZE */
  to {
    transform: rotate(1080deg); }
  /* 4   * ARCSIZE */ }

@keyframes left-spin {
  from {
    transform: rotate(130deg); }
  50% {
    transform: rotate(-5deg); }
  to {
    transform: rotate(130deg); } }

@keyframes right-spin {
  from {
    transform: rotate(-130deg); }
  50% {
    transform: rotate(5deg); }
  to {
    transform: rotate(-130deg); } }

@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fade-out {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

.spinner {
  display: inline-block;
  position: relative;
  width: 28px;
  height: 28px;
  border-color: #9C27B0; }
  .spinner .circle {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
    height: 100%;
    border-width: 3px;
    border-style: solid;
    border-color: inherit;
    border-bottom-color: transparent !important;
    border-radius: 50%; }
  .spinner .gap-patch {
    position: absolute;
    box-sizing: border-box;
    top: 0;
    left: 45%;
    width: 10%;
    height: 100%;
    overflow: hidden;
    border-color: inherit; }
    .spinner .gap-patch .circle {
      width: 1000%;
      left: -450%; }
  .spinner .circle-clipper {
    display: inline-block;
    position: relative;
    width: 50%;
    height: 100%;
    overflow: hidden;
    border-color: inherit; }
    .spinner .circle-clipper.left .circle {
      border-right-color: transparent !important;
      transform: rotate(129deg);
      animation: left-spin 1333ms cubic-bezier(0.4, 0, 0.2, 1) infinite both; }
    .spinner .circle-clipper.right .circle {
      left: -100%;
      border-left-color: transparent !important;
      transform: rotate(-129deg);
      animation: right-spin 1333ms cubic-bezier(0.4, 0, 0.2, 1) infinite both; }
    .spinner .circle-clipper .circle {
      width: 200%; }
  .spinner.cooldown {
    /* duration: SHRINK_TIME */
    animation: fade-out 400ms cubic-bezier(0.4, 0, 0.2, 1) both; }

.spinner-container {
  width: 100%;
  height: 100%;
  border-color: inherit;
  /* duration: 360 * ARCTIME / (ARCSTARTROT + (360-ARCSIZE)) */
  animation: container-rotate 1568ms linear infinite; }

.spinner-layer {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 1;
  border-color: inherit;
  /* durations: 4 * ARCTIME */
  animation: fill-unfill-rotate 5332ms cubic-bezier(0.4, 0, 0.2, 1) infinite both; }

